p,ul{
    padding: 0px;
    margin: 0px;
}
.container{
    background-color: rgba(52, 159, 214, 1);
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
}
.header_nav{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 25px 90px;
}
.header_nav_link{
    text-align: center;
}
.header_logo{
    display: flex;
    flex-wrap: wrap;
    gap:100px;
}
.nav_list{
    list-style-type: none;
    display: flex;
    gap: 26px;
}
.nav_link{
    text-align: center;
}
.nav_link_but1{
    background: none;
    border: none;
    padding: 0px;
    cursor: pointer;
}
.nav_link_but1>svg>path{
    transition: 0.5s;
}
.nav_link_but1:hover>svg>path{
    fill: orange;     
}
.but1_text{
    position: relative;
    top:-5px;
    margin: 0;
    color: white;
}
.nav_employee{
    align-items: center;
    display: flex;
}
.employee_name{
    color:white;
    margin: 0;
}

#kv{
    width: 400px; height: 400px;background-color: white;
}
.kv_close{
    display: none;
}
.n{
    width: 100px;
    height: 100px;
    background-color: black;
}