
.container_table{
	width: 900px;
    height: 300px;
	overflow-x: auto;
   
	margin-top: 0px;
	margin-bottom: 20px;
	border-bottom: 1px solid #eee;
}

table{
    text-align: center;
	table-layout: fixed;
	border: none;
}

th{
    font-weight: bold;
	text-align: left;
	border: none;
	padding: 10px 15px;
	background: #d8d8d8;
	font-size: 14px;
	border-left: 1px solid #ddd;
	border-right: 1px solid #ddd;
}

td{
    text-align: left;
	border-left: 1px solid #ddd;
	border-right: 1px solid #ddd;
	padding: 10px 15px;
	font-size: 14px;
	vertical-align: top;
}

tbody>tr:nth-child(even){
    background: #f3f3f3;
}

::-webkit-scrollbar {
	width: 6px;
} 

::-webkit-scrollbar-track {
	box-shadow: inset 0 0 0px black, 76, 255;
	
} 
::-webkit-scrollbar-thumb {
	background-color: rgba(52, 159, 214, 1);
	border-radius: 20px;
	width: 6px;
} 
::-webkit-scrollbar:horizontal{
	height: 6px;
}
.setingOp{
	display: none;
	position: absolute;
	background-color: rgba(137, 200, 235, 0.2);
	width: 200px;
	left:75%
}
.cont{
	position: relative;
}