.Center{
    display: flex;
}

.centerContainer{
    width: 100%;
}
.CenterTitle{
    display: flex;
    align-items: center;
}
.TitleTitle{
    font-weight: normal;
}


.TitleText{
    font-weight: normal;
}

.TitleText>a{
    color: rgba(52, 159, 214, 1);

}
.TitleText>a:hover{
    color:black;
}
.TitleText>a:active{
    color:black;
}
.TitleText>a:focus{
    color:black;
}
.Center_but{
    color:rgba(52, 159, 214, 1) ;
    border: 1px solid rgba(52, 159, 214, 1);
    background: none;
    border-radius: 30px;
    padding: 5px;
}

.CenterPoisk{
    display: flex;
    gap: 30px;
    padding: 0px 20px;
    align-items: center;

}
.InputCenter_container{
    flex-grow: 1;
}
.InputCenter{
    outline: none;
   width: 100%;
}

.PoiskButt{
    background: none;
    border:none;
}

.poisk_sort{
    display: flex;
}