.MainLeft_container{
    width: 250px;
    height: fit-content;
    background-color: rgba(137, 200, 235, 0.2);
    padding-top: 2em;
    padding-left: 40px;;
    position: relative;
    display: block;
    transform: translateX(0%);
    transition:.2s all linear; 
}
.mimi{
    display: flex;
}
.Main_arr_add{
    transform: translateX(-110%);
    transition:.2s all linear; 
}
.add{
    rotate: 180deg;
    transition: 0.2s;
}
.arre{
    padding: 10px;
    position: absolute;
    z-index: 12;
    cursor: pointer;
}
.Main_arr{
    rotate: 0;
    transition: 0.2s;
    padding: 0;
    border: none;
    background: none;
    align-items: center;
    cursor: pointer;
}
.MainFiltr{
    display: none;
}
.MainFiltr_close{
    display: block;
}
.Filtra{
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    margin-bottom: 10px;
}
.filtr_input_1{
    display: none;
}
.Main_clienst{
    color: rgba(52, 159, 214, 1);
    padding-bottom: 15px;

}
.FiltrOp{
    background: none;
    color: rgba(52, 159, 214, 1) ;
    border:none;
    cursor: pointer;
}
.filtr_input_chek{
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    padding-left: 10px;
}

.Input_mess{
    border: 1px solid rgba(52, 159, 214, 1) ;
    color: rgba(52, 159, 214, 1) ;
        outline: none;
}

.Input_mess::placeholder{color:rgba(52, 159, 214, 1) }
.but_add_mess{
    border: none;
    background: none;
    color:rgba(52, 159, 214, 1);
    cursor: pointer;

}
.div_add{
    color:rgba(52, 159, 214, 1) ;
}

.filtr_input_chek{
    display: none;
}

.filtr_input_chek_close{
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
}